.button {
    height: 36px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 18px;
    width: max-content;
    text-transform: uppercase;
    padding: 0 12px;
    border: none;
}

.button :global(span) {
    font-size: 14px;
    font-weight: 500;
}

.button_color_white {
    background: var(--white);
    color: var(--black);
}